/*****************DEFAULT VALUES*************/
body {
  background-color: var(--background-primary-collor);
  color: var(--font-primary-collor);
  margin: 0;
  padding: 0;
  font-family: var(--font-main);
}

/****************SCROLLBAR*******************/
::-webkit-scrollbar {
  width: 6px;
  height: 6px;
  background-color: transparent;
}

::-webkit-scrollbar-track {
  background: var(--scrollbarBG);
}

::-webkit-scrollbar-thumb {
  background-color: var(--secondary-collor);
  border-radius: 0px;
}

* {
  scrollbar-width: thin;
  scrollbar-color: var(--secondary-collor) var(--scrollbarBG);
}

html {
  scroll-behavior: smooth;
}

::selection {
  background-color: var(--secondary-collor);
}

/****************CUSTOM LINKS*******************/
.link-wrapper {
  position: relative;
  cursor: pointer;
  display: inline-block;
  background: transparent;
}

.link-wrapper:hover .custom-underline::after {
  top: 117%;
  height: 4px;
  -webkit-transition: 0.4s ease all;
  transition: 0.4s ease all;
}

.custom-underline {
  position: relative;
  display: inline-block;
  font-size: var(--font-largest-size);
  font-weight: 200;
  text-align: center;
  color: var(--primary-collor);
  text-decoration: none;
}

.custom-underline::after {
  content: "";
  position: absolute;
  top: 110%;
  height: 3px;
  width: 90%;
  left: 2%;
  background-color: var(--secondary-collor);
  -webkit-transition: 0.3s ease all .1s;
  transition: 0.3s ease all .1s;
}
